.category {
	position: relative;
	display: flex;

	@include adaptive(1023) {
		padding-top: 0;
	}

	&__left {
		width: 43px;
	}

	&__middle {
		position: relative;
		flex: 1;
		min-width: 1px;
		max-width: 100%;
		padding-right: 4px;
		@include adaptive(1023) {
			padding-right: 0;
		}
	}

	&__right {
		width: 111px;
		@include adaptive(1023) {
			width: 43px;

			.category__btn {
				display: none;
			}
		}
	}

	&__btn {
		outline: none;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 34px;
		border-radius: 5px;
		border: none;
		box-shadow: none;
		background-color: rgb(0, 74, 172);
		text-transform: uppercase;
		color: #ffffff;
		font-size: 13px;
		font-weight: 700;

		&:hover{
			color: #fff;
		}
	}

	&__wrapper {
		position: relative;
		display: flex;
		width: 100%;
		height: 100%;
	}

	&__toggle {
		position: relative;
		display: flex;
		width: 100%;
		height: 100%;
		background-color: #ffffff;
		border-radius: 0 5px 5px 0;
		@include adaptive(1023) {
			border-radius: 0;
		}

		input {
			outline: none;
			position: relative;
			flex: 1;
			min-width: 1px;
			max-width: 100%;
			height: 100%;
			padding: 0 12px;
			border: none;
			box-shadow: none;
			border-radius: 0;
			background-color: transparent;
			font-size: 14px;
			font-weight: 700;

			&.searchinput{
				padding: 0 0 0 5px;
			}

			@include inputPlaceholder {
				color: #9294ad;
				font-size: 14px;
				font-weight: 700;
			}
		}

		button {
			outline: none;
			cursor: pointer;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 40px;
			height: 100%;

			border: none;
			border-radius: 0;
			box-shadow: none;
			background-color: transparent;


			&.search_icon{
				width: 25px;
			}

			.icon-font {
				color: #9294ad;
			}
		}
	}

	&__drop {
		overflow: hidden;
		z-index: 1000;
		opacity: 0;
		visibility: hidden;
		transform: translateY(10px);
		position: absolute;
		top: 100%;
		left: 0;
		display: flex;
		flex-direction: column;
		width: 100%;
		border-radius: 5px;
		background-color: #ffffff;
		backface-visibility: hidden;
		@include transition(opacity, visibility, transform);
		@include adaptive(1023) {
			top: calc(100% + 10px);
			left: -63px;
			width: 100vw;
			height: calc(100vh - 54px);
			min-height: 682px;
			border-radius: 0;
			background-color: #080915;
		}

		&.is-open {
			z-index: 1600;
			opacity: 1;
			visibility: visible;
			transform: translateY(0);
			@include transition(opacity, visibility, transform);
		}

		&-head {
			display: flex;
			align-items: center;
			justify-content: space-between;
			min-height: 46px;
			padding: 5px 15px;
			background-color: #0a3e83;
			@include adaptive(1023) {
				padding-right: 0;
				padding-left: 20px;
			}

			> div {
				&:nth-of-type(1) {
					width: 275px;
					@include adaptive(1023) {
						width: auto;
						padding-right: 20px;
					}
				}
				&:nth-of-type(2) {
					display: flex;
					align-items: center;
					justify-content: flex-end;
					flex: 1;
					min-width: 1px;
					max-width: 100%;
				}
			}
		}

		&-body {
			flex: 1;
			min-height: 1px;
			max-height: 100%;
			padding-left: 10px;
			padding-right: 10px;
		}

		&-footer {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 60px;
		}
	}

	&__title {
		color: #ffffff;
		font-size: 18px;
		font-weight: 700;
		@include adaptive(1023) {
			color: #abbde7;
			font-size: 16px;
		}
	}

	&__btn {
		&-wrapper {
			overflow: hidden;
			overflow-x: auto;
			white-space: nowrap;
			position: relative;
			font-size: 0;

			.category__btn {
				display: inline-block;
				vertical-align: middle;
				margin-right: 5px;
				padding-left: 5px;
				padding-right: 5px;
				width: auto;
				min-width: 140px;
				line-height: 33px;

				&:last-of-type {
					margin-right: 0;
				}

				&--1 {
					background-color: #090b27;
				}
				&--2 {
					background-color: #0677ce;
				}
				&--3 {
					background-color: #767676;
				}
				&--4 {
					background-color: #625e73;
				}
				&--5 {
					background-color: #65a6bb;
				}

				img {
					display: inline-block;
					vertical-align: middle;
					max-width: 100%;
					max-height: 100px;
					margin-right: 5px;
				}

				span {
					display: inline-block;
					vertical-align: middle;
					text-transform: capitalize;
					font-size: 13px;
					font-weight: 700;
				}
			}
		}
	}

	&__close {
		display: none;
		align-items: center;
		justify-content: flex-end;
		width: 100%;
		height: 100%;
		cursor: pointer;

		@include adaptive(1023) {
			display: flex;
		}

		.icon-font {
			color: #33529e;
			font-size: 24px;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 210px;
		@include adaptive(1023) {
			height: 100%;
		}

		&-wrapper {
			display: flex;
			flex-wrap: wrap;
			width: 100%;
			@include adaptive(1023) {
				min-height: 100%;
			}

			> div {
				width: 100%;
				max-width: calc(100% / 6);
				padding-left: 5px;
				padding-right: 5px;
				@include adaptive(1535) {
					max-width: calc(100% / 5);
				}
				@include adaptive(1365) {
					max-width: calc(100% / 4);
				}
				@include adaptive(1279) {
					max-width: calc(100% / 3);
				}

				&:nth-last-child(1),
				&:nth-last-child(2) {
					@include adaptive(1535) {
						display: none;
					}
				}

				&:nth-last-child(3),
				&:nth-last-child(4) {
					@include adaptive(1365) {
						display: none;
					}
				}

				&:nth-last-child(4) {
					@include adaptive(1023) {
						display: block;
					}
				}

				&:nth-last-child(5),
				&:nth-last-child(6) {
					@include adaptive(1279) {
						display: none;
					}
					@include adaptive(1023) {
						display: block;
					}
				}
			}
		}
	}

	&__load {
		display: none;
		align-items: center;
		justify-content: center;
		min-width: 150px;
		height: 40px;
		background-color: #cde549;
		color: #080915;
		font-size: 14px;
		text-transform: uppercase;
		@include adaptive(1023) {
			display: flex;
		}
	}

	&__pagination {
		display: flex;
		align-items: center;
		justify-content: center;
		@include adaptive(1023) {
			display: none;
		}

		&-btn,
		&-arrow {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 28px;
			height: 28px;
			margin-left: 7px;
			margin-right: 7px;
		}

		&-btn {
			background-color: #e4e4e4;
			color: #8a8a8a;
			font-size: 18px;
			font-weight: 700;
			@include transition(background-color, color);

			&:hover,
			&.is-active {
				background-color: #07113b;
				color: #d5f34a;
				@include transition(background-color, color);
			}
		}

		&-arrow {
			.icon-font {
				color: #07113b;
				font-size: 18px;
				transform: scaleX(-1);
			}
		}
	}
}

.light{
	.category__btn{
		color: #fff;
	}
}
